
@import url("https://fonts.googleapis.com/css?family=Roboto");
#laravel-style-guide {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
  width: 100%;
}
#laravel-style-guide > div {
    padding: 30px;
    min-height: 100vh;
    background-color: #fafafa;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    min-width: 0;
}
#laravel-style-guide > ul {
    min-width: 200px;
    background-color: #fff;
    border-right: 1px solid #f5f5f5;
    z-index: 1;
    font-family: "Roboto", Helvetica, Arial, sans-serif;
    line-height: 1.2;
}
#laravel-style-guide > ul li:hover {
      background-color: #fafafa;
}
#laravel-style-guide > ul li a {
      display: block;
      padding: 15px 30px;
      border-bottom: 1px solid #e0e0e0;
      text-decoration: none;
      font-weight: bold;
      color: #212121;
      font-size: 1em;
}
#laravel-style-guide .colors {
    display: block;
    padding-bottom: 20px;
}
#laravel-style-guide .colors:after {
      display: block;
      content: "";
      clear: both;
}
#laravel-style-guide .colors .color {
      float: left;
      width: 100px;
      height: 100px;
      border: 1px solid #E0E0E0;
      margin-right: 16px;
      margin-bottom: 16px;
      border-radius: 5px;
      text-align: center;
      padding: 8px;
      box-sizing: border-box;
      font-size: 0.8em;
}
#laravel-style-guide .hljs {
    display: block;
    overflow-x: auto;
    padding: 0.5em;
    font-size: 1.2em;
    line-height: 1.3em;
}
#laravel-style-guide .example {
    border: 1px solid #e5e5e5;
    margin-bottom: 1em;
    padding: 20px 20px 0;
    border-radius: 3px;
    background-color: white;
}
#laravel-style-guide .example pre {
      margin: 20px -20px 0;
}
#laravel-style-guide code.hljs.inline {
    display: inline;
    padding: 0.2em 0.5em;
    font-size: 0.9em;
}



